<template>
    <div class="container">
       <div class="main__cars" v-if="posts">
        <div class="main__cars-item" v-for="post in posts" :key="post.id">
            <p>ID: {{ post.id }}</p>
            <p>Title: {{ post.title }}</p>
            <p>Body: {{ post.body }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                posts: null
            }
        },
        created() {
            this.getPosts()
        },
        methods: {
            async getPosts() {
                let res = await fetch('https://jsonplaceholder.typicode.com/posts')
                let data = await res.json()
                this.posts = data
            }
        },
    }
</script>